.page-timer {
  color: #fff;
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  &__container {

  }
}
.js-timer {
  padding-bottom: 50px;
  margin-top: 90px;
 span {
   color: #e9e9e9!important;
   font-family   : "Antenna-Bold"!important;
   //font-size: 64px;
   @media (max-width: 992px) {
     font-size: 38px;
   }
 }
  .timer-element {
    position: relative;
    min-width: 90px;
    @media (max-width: 992px) {
      min-width: 53px;
    }
    &:before {
      content: "";
      width: 1px;
      height: 100px;
      background: #e9e9e9;
      right: -20px;
      z-index: 50;
      position: absolute;
      opacity: 0.45;
      @media (max-width: 992px) {
        height: 87px;
      }
    }
    &:nth-child(1) {
      &:after {
        content: "Days";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(3) {
      &:after {
        content: "Hours";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(5) {
      &:after {
        content: "Minutes";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(7) {
      &:after {
        content: "Seconds";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
      &:before {
        content: "";
        width: 1px;
        height: 100px;
        background: #e9e9e9;
        right: -20px;
        z-index: 50;
        position: absolute;
        opacity: 0.45;
        display: none;
      }
    }
  }
}

.logo {
  display: flex;
  justify-content: center;
  img {
    height: auto;
    @media (max-width: 768px) {
      width: 80%;
    }
  }
}
.description {
  font-family   : "Antenna-Bold";
  font-weight: 500;
  font-size: 36px;
  line-height: 104%;
  margin: 40px 0;
  text-align: center;
  @media (max-width: 768px) {
    font-size: 18px;
    line-height: 104%;
  }
}
.info-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  &__top, &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__bottom {
    margin-top: 24px;
    @media (max-width: 768px) {
      margin-top: 14px;
    }
  }
  &__item {
    width: calc(50% - 0px);
    @media (max-width: 768px) {
      width: calc(50% - 3px);
    }
    svg {
      width: 100%;
      transition: transform 0.3s;
      height: 100%;
      @media (max-width: 992px) {
        height: auto;
      }
    }
    &:hover {
      svg {
        transform: scale3d(1.05,1.05,1.05);
        transition: transform 0.3s;
      }
    }
  }

  &__audit {
    transform: translateX(15px);
    @media (max-width: 992px) {
      transform: translateX(7px);
    }
  }
  &__whitepaper {
    transform: translateX(-15px);
    @media (max-width: 992px) {
      transform: translateX(-7px);
    }
  }

  &__telegram {
    transform: translateX(15px);
    svg {
      &:last-child {
        display: none;
      }
    }

    @media (max-width: 992px) {
      transform: translateX(7px);
    }
    @media (max-width: 768px) {
      width: calc(57% - 3px);
      svg {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block;
        }
      }
    }
  }
  &__dx {
    transform: translateX(-15px);
    svg {
      &:last-child {
        display: none;
      }
    }
    @media (max-width: 992px) {
      transform: translateX(-7px);
    }
    @media (max-width: 768px) {
      width: calc(43% - 3px);
      svg {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block;
        }
      }
    }
  }
}
